.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__centr {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch__second {
  transform-origin: 50% 100%;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.stopwatch__minute {
  transform-origin: 50% 100%;
  animation: steps(60, end) infinite rotate;
  animation-duration: 3600;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -100%)rotate(0);
  }

  100% {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch--speed-up .stopwatch__second {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__minute {
  animation-duration: 600s;
}
