@import 'utils/variables';
@import 'utils/mixins';
@import 'blocks/stopwatch';

body {
  margin: 0;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.stopwatch--speed-up {
  .stopwatch__second {
    animation-duration: 10s;
  }

  .stopwatch__minute {
    animation-duration: 600s;
  }
}
